<template>
  <div id="app">
    <div class="content">
      <h2>注册 Register</h2>
      <!-- 可以使用 autofocus/使用自定义指令 来实现自动聚焦，但是这里由于使用了keep-alive，所以只有在第一次进入时inserted函数才会生效 -->
      <!-- 当组件被缓存时，它在DOM中已经被插入过了，所以inserted方法在组件首次被插入时才会执行。 -->
      <label>用户名：</label><input name="name" v-model="name" v-focus ref="nameInput" maxlength="20" /><br>
      <label>密 码：</label><input name="password" v-model="password" maxlength="20" /><br>
      <button @click="clickRegister">注册</button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import { throttle } from 'lodash'

export default {
  name: 'RegisterPage',
  data () {
    return {
      name: '',
      password: ''
    }
  },
  computed: {
    ...mapState('test', ['BaseURL'])
  },
  methods: {
    clickRegister () {
      if (this.name.length < 6 || this.password.length < 8) {
        this.$message.warning('用户名不少于6位，密码要求不少于8位')
      } else {
        this.throttleRegister()
      }
    },
    // register()方法的节流处理，点击不发送，计时1.5s，期间再次点击则重新计时
    throttleRegister: throttle(function () { this.register() }, 1500),
    // throttleRegister: throttle(() => { this.register() }, 1500),
    register () {
      console.log('register')

      axios({
        url: `${this.BaseURL}/user/register`,
        method: 'POST',
        data: {
          name: this.name,
          password: this.password
        }
      }).then((response) => {
        // console.log(response)
        if (response.data.code === 1) {
          this.$message({
            type: 'success',
            message: '注册成功',
            duration: 3000
          })
          // 在注册成功后可以弹出一个选项框，让用户选择是否取登录，去登陆的话，帮用户把name和password填好
          setTimeout(() => {
            const name = this.name
            const password = this.password
            const notification = this.$notification({
              title: '',
              message: '注册成功，点我去登录',
              duration: 5000,
              onClick: () => {
                notification.close()
                this.$router.push({
                  path: '/login',
                  query: {
                    name,
                    password
                  }
                })
              }
            })
            this.name = ''
            this.password = ''
          }, 1000)
        } else {
          this.$message({
            type: 'warning',
            message: response.data.msg,
            duration: 3000
          })
        }
      })
    }
  },
  activated () {
    this.$refs.nameInput.focus()
  }
}
</script>

<style scoped lang="less">
#app {
  .content {
    text-align: center;
    line-height: 100px;
    font-weight: bold;
    font-size: 20px;
    margin-top: 50px;

    h2 {
      margin: 0;
    }

    label {
      display: inline-block;
      width: 80px;
    }

    button {
      font-weight: bold;
      border: 0;
      border-radius: 10px;
      width: 200px;
      height: auto;
      padding: 10px 10px;
      background-color: #42B983;
      font-size: 20px;
      color: white;
    }

    button:hover {
      background-color: #3498DB;
    }

    input {
      margin-right: 8px;
      width: 180px;
      height: auto;
      font-size: large;
      border: rgb(218, 218, 218) solid 1px;
      border-radius: 5px;
      padding: 8px;
    }
  }
}
</style>
