<template>
  <div id="app">
    <div class="content">
      <h2>登录 Login</h2>
      <!-- 可以使用 autofocus/使用自定义指令 来实现自动聚焦，但是这里由于使用了keep-alive，所以只有在第一次进入时inserted函数才会生效 -->
      <!-- 当组件被缓存时，它在DOM中已经被插入过了，所以inserted方法在组件首次被插入时才会执行。 -->
      <label>用户名：</label><input name="name" v-model="name" v-focus ref="nameInput" /><br>
      <label>密 码：</label><input type="password" name="password" v-model="password" /><br>
      <button @click="clickLogin">登录</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import { debounce } from 'lodash'

export default {
  name: 'LoginPage',
  data () {
    return {
      name: '',
      password: ''
    }
  },
  computed: {
    ...mapState('test', ['BaseURL'])
  },
  methods: {
    // login()方法的 节流（点击发送，但是1.5s内再次点击不会再发送） 处理
    clickLogin () {
      if (this.name.length < 6 || this.password.length < 8) {
        this.$message.warning('用户名不少于6位，密码要求不少于8位')
      } else {
        this.debounceLogin()
      }
    },
    debounceLogin: debounce(function () { this.login() }, 1500),
    login () {
      console.log('login')
      axios({
        url: `${this.BaseURL}/user/login`,
        method: 'POST',
        // 设置超时请求世间为5s
        timeout: 5000,
        data: {
          name: this.name,
          password: this.password
        }
      }).then((response) => {
        if (response.data >= 1) {
          this.$message({
            message: '登录成功',
            type: 'success',
            duration: 3000
          })
          this.$router.push('/')
        } else {
          alert('账号或密码错误')
          this.name = ''
          this.password = ''
        }
        // console.log(response)
      }).catch((error) => {
        // 如果请求不成功，要么后端服务器没启动，要么BaseURL变了，所以要用户重新设置BaseURL
        alert('BaseURL已过期，请重新设置')
        this.$router.push('/test')
        console.log(error)
      })
    }

  },
  activated () {
    // 配合v-focus实现自动聚焦到输入框
    this.$refs.nameInput.focus()
    // 判断 this.$route.query 是否为空对象
    if (Object.keys(this.$route.query).length !== 0) {
      this.name = this.$route.query.name
      this.password = this.$route.query.password
    }
  }
}
</script>

<style scoped lang="less">
#app {
  .content {
    text-align: center;
    line-height: 100px;
    font-weight: bold;
    font-size: 20px;
    margin-top: 50px;

    h2 {
      margin: 0;
    }

    label {
      display: inline-block;
      width: 80px;
    }

    button {
      font-weight: bold;
      border: 0;
      border-radius: 10px;
      width: 200px;
      height: auto;
      padding: 10px 10px;
      background-color: #42B983;
      font-size: 20px;
      color: white;
    }

    button:hover {
      background-color: #3498DB;
    }

    input {
      margin-right: 8px;
      width: 180px;
      height: auto;
      font-size: large;
      border: rgb(218, 218, 218) solid 1px;
      border-radius: 5px;
      padding: 8px;
    }
  }
}
</style>
